import { Subscription } from 'rxjs';
import { ComponentRef, EmbeddedViewRef } from '@angular/core';

export class ComponentInstance<T> {
    private subscriptions = new Subscription();
    private destroyActions = [];

    get element() {
        return (this.componentRef.hostView as EmbeddedViewRef<any>)
            .rootNodes[0] as HTMLElement;
    }

    constructor(
        private componentRef: ComponentRef<T>,
    ) {}

    setProp(key: string, value: any) {
        if (!key || !value) {
            return this;
        }
        this.componentRef.instance[key] = value;
        return this;
    }

    onEvent(key: string, callback: (...args: any) => any) {
        if (!key || !callback) {
            return this;
        }
        if (!this.componentRef.instance[key]) {
            return this;
        }
        this.subscriptions.add(
            this.componentRef.instance[key].subscribe(callback)
        );
        return this;
    }

    onDestroy(callback: () => void) {
        this.destroyActions.push(callback);
        return this;
    }

    destroy() {
        this.subscriptions.unsubscribe();
        this.componentRef.destroy();
        this.destroyActions.forEach(action => action());
        this.destroyActions = [];
    }
}
