import { Injectable, Injector, ComponentFactoryResolver, EmbeddedViewRef, ApplicationRef } from '@angular/core';
import { ComponentInstance } from './component-instance.model';

@Injectable({
    providedIn: 'root'
})
export class ComponentService {

    constructor(
        private componentFactoryResolver: ComponentFactoryResolver,
        private appRef: ApplicationRef,
        private injector: Injector
    ) {}

    instantiate<T = any>(component: any) {
        const componentRef = this.componentFactoryResolver
            .resolveComponentFactory<T>(component)
            .create(this.injector);

        this.appRef.attachView(componentRef.hostView);

        const instance = new ComponentInstance<T>(
            componentRef
        );

        instance.onDestroy(
            () => this.appRef.detachView(componentRef.hostView)
        );

        return instance;
    }
}
