import { Component, Input, Output, EventEmitter, forwardRef } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, FormControl } from '@angular/forms';
import { constants } from '../../components.constants';

@Component({
    selector: 'zip-input',
    templateUrl: './input.component.html',
    styleUrls: ['./input.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => InputComponent),
            multi: true
        }
    ]
})
export class InputComponent implements ControlValueAccessor {
    @Input() label = '';
    @Input() placeholder = '';
    @Input() type = 'text';
    @Input() iconLeft = '';
    @Input() iconRight = '';
    @Input() formControl = new FormControl();
    @Input() errorMessages = {};
    @Input() value = '';

    get pending() {
        return this.formControl.status === 'PENDING';
    }

    get invalid() {
        return this.formControl.touched && this.formControl.status === 'INVALID';
    }

    get error() {
        if (!this.formControl.errors) {
            return null;
        }
        const error = Object.keys(this.formControl.errors)
            .map(validationError => this.errorMessages[validationError])[0];

        return error || constants.messages.invalidInput;
    }

    writeValue(value: any) {
        if (value === this.formControl.value) {
            return;
        }
        this.formControl.setValue(value);
        this.onChangeCallback(value);
    }

    registerOnChange(fn: any) {
        this.onChangeCallback = fn;
    }

    registerOnTouched(fn: any) {
        this.onTouchedCallback = fn;
    }

    onTouchedCallback: () => void = () => { };
    private onChangeCallback: (_: any) => void = () => { };
}
