import { Component, Input, Output, EventEmitter, HostBinding } from '@angular/core';
import { noticeAnimation } from './input-notice.animation';

@Component({
    selector: 'zip-input-notice',
    template: '{{ message }}',
    styleUrls: ['./input-notice.component.scss'],
    animations: noticeAnimation
})
export class InputNoticeComponent {
    @HostBinding('class') color;
    @HostBinding('@in-out')

    @Input() message;
    @Input() set theme(value) {
        this.color = value;
    }
}
