import { Component, Input, HostBinding } from '@angular/core';

@Component({
  selector: 'zip-icon',
  template: '',
  styleUrls: ['./icon.component.scss']
})
export class IconComponent {
    @HostBinding('class') icon = '';
    @Input() set src(value) {
        this.icon = `zip-icon zip-icon-${value}`;
    }
}
