import { Component, Input } from '@angular/core';

@Component({
    selector: 'zip-header-cart',
    template: `
        <div>{{ items }}</div>
        <zip-icon src="cart"></zip-icon>
        <p>{{ parsedAmount }}</p>
    `,
    styleUrls: ['./header-cart.component.scss']
})
export class HeaderCartComponent {
    public parsedAmount = '00.00';

    @Input()
    public items = 0;

    @Input()
    public set amount(value) {
        this.parsedAmount = parseFloat(value).toFixed(2);
    }
    public get amount() {
        return this.parsedAmount;
    }
}
